import songlist from '@/components/songlist.vue'
export default {
  data() {
    return {
      songrankinglist:[],
      hotsonglist:[],
      sendsonglist:[],
      isshowlist: false
    };
  },
  components:{
    songlist
  },
  mounted(){
    uni.request({
      url: 'http://39.107.80.8:5000/toplist',
      method: 'GET',
      data: {},
      success: res => {
        const data = res.data.list.splice(0,6)
        this.songrankinglist= data.map((item,idx) => {
          return {
            name:item.name,
            imgurl: item.coverImgUrl,
            id: idx
          }
        })
      },
      fail: () => {},
      complete: () => {}
    })
    uni.request({
      url: 'http://39.107.80.8:5000/personalized?limit=30',
      method: 'GET',
      data: {},
      success: res => {
        const data = res.data.result
        this.hotsonglist = data.map((item,idx) => {
          return {
            name:item.name,
            imgurl: item.picUrl,
            id: item.id
          }
        })
      },
      fail: () => {},
      complete: () => {}
    })
  },
  methods:{
    changeisshowlist(val){
      this.isshowlist = val
    },
    sendlist1(id){
      uni.showLoading({
          title: '加载中',
          mask: true
      });
      
      uni.request({
        url: 'http://39.107.80.8:5000/top/list?idx='+id
        ,
        method: 'GET',
        data: {},
        success: res => {
          const data = res.data.playlist.tracks.splice(0, 100)
          this.sendsonglist = data.map(item => {
            let sin = ''
            item.ar.forEach((itm, idx) => {
              if (idx == 0) {
                sin = itm.name
              } else {
                sin = sin + '/' + itm.name
              }
            })
            return {
              name: item.name,
              singer: item.ar.length == 1 ? item.ar[0].name : sin,
              id: item.id,
              picurl: item.al.picUrl
            }
          })
          this.isshowlist = true
          uni.hideLoading()
        },
        fail: () => {},
        complete: () => {}
      });
    },
    sendlist2(id){
      uni.showLoading({
          title: '加载中',
          mask: true
      });
      
      uni.request({
        url: 'http://39.107.80.8:5000/playlist/detail?id='+id
        ,
        method: 'GET',
        data: {},
        success: res => {
          const data = res.data.playlist.tracks.splice(0, 100)
          this.sendsonglist = data.map(item => {
            let sin = ''
            item.ar.forEach((itm, idx) => {
              if (idx == 0) {
                sin = itm.name
              } else {
                sin = sin + '/' + itm.name
              }
            })
            return {
              name: item.name,
              singer: item.ar.length == 1 ? item.ar[0].name : sin,
              id: item.id,
              picurl: item.al.picUrl
            }
          })
          this.isshowlist = true
          uni.hideLoading()
        },
        fail: () => {},
        complete: () => {}
      });
    }
  }
}
